import { useMemo } from "react";
import Image from "next/image";
import Router from "next/router";
import moment from "moment";
import { Button, Box, Card, CardActionArea, CardActions, CardContent, CardMedia, Divider, Grid, Typography, useMediaQuery, useTheme } from "@mui/material";
import { routes } from "../routes";
import { getEventUrlFromTitleDate } from "../utils/date";
import { getRemainingTicketsText } from "../utils/eventCard";

import { POUND_SYMBOL, IMG_PLACEHOLDER } from "../constants";

import femaleSVG from '../../public/images/female.svg';
import maleSVG from '../../public/images/male.svg';
import { ArrowRightAlt } from "@mui/icons-material";

export default function EventCard({
  id,
  imgURL,
  imgMobileUrl,
  title,
  dateTime,
  venue,
  ageRangeFrom,
  ageRangeTo,
  menAvailableTickets,
  womenAvailableTickets,
  ticketPrice,
  ticketPriceWithoutDiscount
}) {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'))

  const imageUrlForCard = useMemo(() => {
    if (isMobile && imgMobileUrl) {
      return imgMobileUrl
    }

    if (imgURL) { return imgURL }

    return IMG_PLACEHOLDER;
  }, [isMobile, imgURL])

  return (
    <Card sx={{
      width: '100%',
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
      '& .MuiCardContent-root': {
        padding: 0,
        height: 'inherit',
      },
      '& .MuiCardActions-root': {
        display: 'initial',
        padding: 0,
      }
    }}>
      <CardActionArea onClick={() => { Router.push(`${routes.events}/${getEventUrlFromTitleDate(title, dateTime)}`) }}>
        <Box sx={{
          position: 'relative',
          height: 250,
          '& img': {
            objectFit: 'cover'
          }
        }}>
          <Image src={imageUrlForCard} alt={title} fill />
        </Box>
        <Grid
          container
          alignItems='center'
          justifyContent='center'
          sx={{
            backgroundColor: 'primary.main',
            color: 'primary.contrastText',
            '& *': {
              whiteSpace: 'nowrap',
              overflow: 'hidden',
              textOverflow: 'ellipsis',
            },
          }}
        >
          <Box p={1} textAlign='center' maxWidth='100%'>
            <Typography variant='h6'>{title}</Typography>
            <Typography variant='body2' component='div'>
              <Box fontWeight='fontWeightMedium'>{venue}</Box>
            </Typography>
          </Box>
        </Grid>
      </CardActionArea>
      <CardContent>
        <Box py={2}>
          <Grid container justifyContent='space-evenly'>
            <Grid item container xs={5} alignItems='center' justifyContent='center' wrap='nowrap'>
              <Box maxWidth={24}>
                <Image src={maleSVG} alt='Male Icon' style={{ width: '100%' }} />
              </Box>
              <Typography variant='body2' component='div'>
                <Box ml={2} fontWeight='fontWeightBold'>
                  {getRemainingTicketsText(menAvailableTickets)}
                </Box>
              </Typography>
            </Grid>
            <Grid>
              <Divider sx={{ height: '100%', width: 1 }} />
            </Grid>
            <Grid item container xs={5} alignItems='center' justifyContent='center' wrap='nowrap'>
              <Box maxWidth={26}>
                <Image src={femaleSVG} alt='Female Icon' style={{ width: '100%' }} />
              </Box>
              <Typography variant='body2' component='div'>
                <Box ml={2} fontWeight='fontWeightBold'>
                  {getRemainingTicketsText(womenAvailableTickets)}
                </Box>
              </Typography>
            </Grid>
          </Grid>
        </Box>
        <Box paddingLeft={2} paddingRight={2}>
          <Grid container>
            <Grid item xs>
              <Box textAlign='center'>
                <Typography variant='body1' component='div'>
                  <Box fontWeight='fontWeightBold'>Date</Box>
                </Typography>
                <Typography variant='body2' component='p'>
                  {dateTime ? moment(dateTime).format('dddd, Do MMMM YYYY') : null}
                </Typography>
              </Box>
            </Grid>
            <Grid>
              <Divider sx={{ height: '100%', width: 1 }} />
            </Grid>
            <Grid item xs>
              <Box textAlign='center'>
                <Typography variant='body1' component='div'>
                  <Box fontWeight='fontWeightBold'>Age Range</Box>
                </Typography>
                <Typography variant='h6'>
                  {ageRangeFrom}-{ageRangeTo}
                </Typography>
              </Box>
            </Grid>
            <Grid>
              <Divider sx={{ height: '100%', width: 1 }} />
            </Grid>
            <Grid item xs>
              <Box textAlign='center'>
                <Typography variant='body1' component='div'>
                  <Box fontWeight='fontWeightBold'>Price</Box>
                </Typography>
                <Typography variant='h6'>
                  {ticketPriceWithoutDiscount && 
                    <>                      
                      <Typography sx={{
                        color: 'primary.main',
                        textDecoration: 'line-through',
                        textDecorationThickness: '1px',
                        marginRight: '4px',
                      }}>{POUND_SYMBOL}{ticketPriceWithoutDiscount}</Typography>
                    </>
                  }
                  {POUND_SYMBOL}
                  {ticketPrice}
                </Typography>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </CardContent>
      <CardActions>
        <Box p={2}>
          <Button
            fullWidth
            variant='contained'
            color='primary'
            endIcon={<ArrowRightAlt />}
            onClick={() => Router.push(`${routes.events}/${getEventUrlFromTitleDate(title, dateTime)}`)}
          >
            Book Now
          </Button>
        </Box>
      </CardActions>
    </Card>
  )
}