import { useState, useMemo, useCallback, useEffect } from "react";
import { Autocomplete, Box, Button, Container, Grid, MenuItem, TextField, Typography } from "@mui/material";
import { DatePicker } from "@mui/x-date-pickers";
import { ArrowRightAlt } from "@mui/icons-material";

import { COLORS } from '../theme'
import { useEvents } from "../contexts/EventsProvider";

import PageSpinner from "./PageSpinner";
import FeaturedEventCard from "./FeaturedEventCard";
import EventCard from "./EventCard";

export default function EventSearch({ events, title, limitCards, bgColor }) {
  const [searchResult, setSearchResult] = useState(null);
  const [date, setDate] = useState(null);
  const [location, setLocation] = useState();
  const [ageRange, setAgeRange] = useState();

  // useEffect(() => {
  //   if (!eventsLoaded && !loading) {
  //     loadEvents();
  //   }
  // }, [eventsLoaded, loadEvents, loading]);

  const ageRanges = useMemo(() => {
    const allRanges = events
      .map(({ ageRangeFrom, ageRangeTo }) => `${ageRangeFrom} - ${ageRangeTo}`)
      .sort();

    return [...new Set(allRanges)];
  }, [events]);

  const handleSearch = useCallback(
    (e) => {
      e.preventDefault();
      let searchResult = [...events];

      if (location) {
        searchResult = searchResult.filter(({ venue }) => venue === location);
      }

      if (ageRange) {
        try {
          // get the values from the selected ageRange string
          const [from, to] = ageRange?.split('-').map((s) => s.trim());
          searchResult = searchResult.filter(
            ({ ageRangeFrom, ageRangeTo }) => ageRangeFrom >= from && ageRangeTo <= to
          );
        } catch (error) {
          // do nothing
        }
      }

      if (date) {
        searchResult = searchResult.filter(
          ({ dateTime }) =>
            new Date(dateTime).toLocaleDateString() === new Date(date).toLocaleDateString()
        );
      }

      setSearchResult(searchResult);
    },
    [ageRange, date, events, location]
  );

  const finalEventsArray = useMemo(() => {
    const featuredEvents = [];
    const basicEvents = [];
    (searchResult || events).forEach((e) => {
      if (!e.archived) {
        if (e.featured) {
          featuredEvents.push(e);
        } else {
          basicEvents.push(e);
        }
      }
    });

    return [...featuredEvents, ...basicEvents];
  }, [events, searchResult]);

  return (
    <>
      <Box bgcolor={bgColor || COLORS.lightGrey} minHeight='inherit'>
        <Container>
          <Box>
            {
              events.length > 0 ? (
                <Typography variant={title ? 'h4' : 'h1'} sx={(theme) => ({
                  textTransform: 'capitalize',
                  textAlign: 'center',
                  fontSize: '32px',
                  paddingTop: theme.spacing(4),
                  '& .event-span': {
                    color: theme.palette.primary.main,
                  },
                  [theme.breakpoints.down('xs')]: {
                    fontSize: 30,
                  },
                })} >
                  {title || (
                    <>
                      <strong>Find your</strong> Perfect Dating Events in London
                    </>
                  )}
                </Typography>
              ) : (
                <Box textAlign='center' pt={10}>
                  <Typography variant='h6'>We currently have no events available...</Typography>
                </Box>
              )
            }
            <Box my={4}>
              <form onSubmit={handleSearch}>
                <Grid container spacing={2}>
                  <Grid item xs={12} sm={12} md>
                    <Autocomplete
                      fullWidth
                      options={events.length ? [...new Set(events.map(({ venue }) => venue))] : []}
                      getOptionLabel={(option) => option}
                      renderInput={(params) => (
                        <TextField {...params} label="Location" variant="outlined" />
                      )}
                      onChange={(_, newValue) => {
                        setLocation(newValue);
                      }}
                      sx={(theme) => ({
                        backgroundColor: theme.palette.grey[200],
                      })}
                    />
                  </Grid>
                  <Grid item xs={6} sm>
                    <TextField
                      fullWidth
                      onChange={(e) => setAgeRange(e.target.value)}
                      label='Age'
                      select
                      variant='outlined'
                      defaultValue=''
                      sx={(theme) => ({
                        backgroundColor: theme.palette.grey[200],
                      })}
                    >
                      <MenuItem key={`ageRange_None`} value={''}>
                        <em>None</em>
                      </MenuItem>
                      {ageRanges.map((range, i) => {
                        return (
                          <MenuItem key={`ageRange_${i}`} value={range}>
                            {range}
                          </MenuItem>
                        );
                      })}
                    </TextField>
                  </Grid>
                  <Grid item xs={6} sm>
                    <DatePicker
                      autoOk
                      disableToolbar
                      fullWidth
                      variant='inline'
                      inputVariant='outlined'
                      label='Date'
                      format='DD/MM/YYYY'
                      value={date}
                      onChange={(date) => setDate(date)}
                      sx={(theme) => ({
                        backgroundColor: theme.palette.grey[200]
                      })}
                    />
                  </Grid>
                  <Grid item>
                    <Box height='100%' display='flex' alignItems='center'>
                      <Button
                        size={title ? 'large' : 'medium'}
                        fullWidth
                        variant='contained'
                        color='primary'
                        endIcon={<ArrowRightAlt />}
                        type='submit'
                      >
                        Search
                      </Button>
                    </Box>
                  </Grid>
                </Grid>
              </form>
            </Box>
            <Grid container spacing={2}>
              {
                events.length > 0 && (
                  (limitCards ? finalEventsArray.slice(0, limitCards) : finalEventsArray).map(
                    (event, index) =>
                      event.featured ? (
                        <Grid item container key={`event_${index}`}>
                          <FeaturedEventCard {...event} />
                        </Grid>
                      ) : (
                        <Grid item key={`event_${index}`} xs={12} sm={6} md={4}>
                          <EventCard {...event} />
                        </Grid>
                      )
                  )
                )
              }
            </Grid>
          </Box>
        </Container>
      </Box>
    </>
  )
}